        'He unlocked the door and I picked up my hat and stick and stepped into the corridor. The stairs were dark. Groping about, I set my foot on something soft, which snarled and spit, and I aimed a murderous blow at the cat, but my cane shivered to splinters against the balustrade, and the beast scurried back into Mr. Wilde\'s room.\n' +
        '\n' +
        'Passing Hawberk\'s door again I saw him still at work on the armour, but I did not stop, and stepping out into Bleecker Street, I followed it to Wooster, skirted the grounds of the Lethal Chamber, and crossing Washington Park went straight to my rooms in the Benedick. Here I lunched comfortably, read the Herald and the Meteor, and finally went to the steel safe in my bedroom and set the time combination. The three and three-quarter minutes which it is necessary to wait, while the time lock is opening, are to me golden moments. From the instant I set the combination to the moment when I grasp the knobs and swing back the solid steel doors, I live in an ecstasy of expectation. Those moments must be like moments passed in Paradise. I know what I am to find at the end of the time limit. I know what the massive safe holds secure for me, for me alone, and the exquisite pleasure of waiting is hardly enhanced when the safe opens and I lift, from its velvet crown, a diadem of purest gold, blazing with diamonds. I do this every day, and yet the joy of waiting and at last touching again the diadem, only seems to increase as the days pass. It is a diadem fit for a King among kings, an Emperor among emperors. The King in Yellow might scorn it, but it shall be worn by his royal servant.\n' +
        '\n' +
        'I held it in my arms until the alarm in the safe rang harshly, and then tenderly, proudly, I replaced it and shut the steel doors. I walked slowly back into my study, which faces Washington Square, and leaned on the window sill. The afternoon sun poured into my windows, and a gentle breeze stirred the branches of the elms and maples in the park, now covered with buds and tender foliage. A flock of pigeons circled about the tower of the Memorial Church; sometimes alighting on the purple tiled roof, sometimes wheeling downward to the lotos fountain in front of the marble arch. The gardeners were busy with the flower beds around the fountain, and the freshly turned earth smelled sweet and spicy. A lawn mower, drawn by a fat white horse, clinked across the green sward, and watering-carts poured showers of spray over the asphalt drives. Around the statue of Peter Stuyvesant, which in 1897 had replaced the monstrosity supposed to represent Garibaldi, children played in the spring sunshine, and nurse girls wheeled elaborate baby carriages with a reckless disregard for the pasty-faced occupants, which could probably be explained by the presence of half a dozen trim dragoon troopers languidly lolling on the benches. Through the trees, the Washington Memorial Arch glistened like silver in the sunshine, and beyond, on the eastern extremity of the square the grey stone barracks of the dragoons, and the white granite artillery stables were alive with colour and motion.\n' +
        '\n' +
        'I looked at the Lethal Chamber on the corner of the square opposite. A few curious people still lingered about the gilded iron railing, but inside the grounds the paths were deserted. I watched the fountains ripple and sparkle; the sparrows had already found this new bathing nook, and the basins were covered with the dusty-feathered little things. Two or three white peacocks picked their way across the lawns, and a drab coloured pigeon sat so motionless on the arm of one of the "Fates," that it seemed to be a part of the sculptured stone.\n' +
        '\n' +
        'As I was turning carelessly away, a slight commotion in the group of curious loiterers around the gates attracted my attention. A young man had entered, and was advancing with nervous strides along the gravel path which leads to the bronze doors of the Lethal Chamber. He paused a moment before the "Fates," and as he raised his head to those three mysterious faces, the pigeon rose from its sculptured perch, circled about for a moment and wheeled to the east. The young man pressed his hand to his face, and then with an undefinable gesture sprang up the marble steps, the bronze doors closed behind him, and half an hour later the loiterers slouched away, and the frightened pigeon returned to its perch in the arms of Fate.\n' +
        '\n' +
        'I put on my hat and went out into the park for a little walk before dinner. As I crossed the central driveway a group of officers passed, and one of them called out, "Hello, Hildred," and came back to shake hands with me. It was my cousin Louis, who stood smiling and tapping his spurred heels with his riding-whip.\n' +
        '\n' +
        '"Just back from Westchester," he said; "been doing the bucolic; milk and curds, you know, dairy-maids in sunbonnets, who say \'haeow\' and \'I don\'t think\' when you tell them they are pretty. I\'m nearly dead for a square meal at Delmonico\'s. What\'s the news?"\n' +
        '\n' +
        '"There is none," I replied pleasantly. "I saw your regiment coming in this morning."\n' +
        '\n' +
        '"Did you? I didn\'t see you. Where were you?"\n' +
        '\n' +
        '"In Mr. Wilde\'s window."\n' +
        '\n' +
        '"Oh, hell!" he began impatiently, "that man is stark mad! I don\'t understand why you"\n' +
        '\n' +
        'He saw how annoyed I felt by this outburst, and begged my pardon.\n' +
        '\n' +
        '"Really, old chap," he said, "I don\'t mean to run down a man you like, but for the life of me I can\'t see what the deuce you find in common with Mr. Wilde. He\'s not well bred, to put it generously; he is hideously deformed; his head is the head of a criminally insane person. You know yourself he\'s been in an asylum"\n' +
        '\n' +
        '"So have I," I interrupted calmly.\n' +
        '\n' +
        'Louis looked startled and confused for a moment, but recovered and slapped me heartily on the shoulder. "You were completely cured," he began; but I stopped him again.\n' +
        '\n' +
        '"I suppose you mean that I was simply acknowledged never to have been insane."\n' +
        '\n' +
        '"Of course thatthat\'s what I meant," he laughed.\n' +
        '\n' +
        'I disliked his laugh because I knew it was forced, but I nodded gaily and asked him where he was going. Louis looked after his brother officers who had now almost reached Broadway.\n' +
        '\n' +
        '"We had intended to sample a Brunswick cocktail, but to tell you the truth I was anxious for an excuse to go and see Hawberk instead. Come along, I\'ll make you my excuse."\n' +
        '\n' +
        'We found old Hawberk, neatly attired in a fresh spring suit, standing at the door of his shop and sniffing the air.\n' +
        '\n' +
        '"I had just decided to take Constance for a little stroll before dinner," he replied to the impetuous volley of questions from Louis. "We thought of walking on the park terrace along the North River."\n' +
        '\n' +
        'At that moment Constance appeared and grew pale and rosy by turns as Louis bent over her small gloved fingers. I tried to excuse myself, alleging an engagement uptown, but Louis and Constance would not listen, and I saw I was expected to remain and engage old Hawberk\'s attention. After all it would be just as well if I kept my eye on Louis, I thought, and when they hailed a Spring Street horse-car, I got in after them and took my seat beside the armourer.\n' +
        '\n' +
        'The beautiful line of parks and granite terraces overlooking the wharves along the North River, which were built in 1910 and finished in the autumn of 1917, had become one of the most popular promenades in the metropolis. They extended from the battery to 190th Street, overlooking the noble river and affording a fine view of the Jersey shore and the Highlands opposite. Cafs and restaurants were scattered here and there among the trees, and twice a week military bands from the garrison played in the kiosques on the parapets.\n' +
        '\n' +
        'We sat down in the sunshine on the bench at the foot of the equestrian statue of General Sheridan. Constance tipped her sunshade to shield her eyes, and she and Louis began a murmuring conversation which was impossible to catch. Old Hawberk, leaning on his ivory headed cane, lighted an excellent cigar, the mate to which I politely refused, and smiled at vacancy. The sun hung low above the Staten Island woods, and the bay was dyed with golden hues reflected from the sun-warmed sails of the shipping in the harbour.\n' +
        '\n' +
        'Brigs, schooners, yachts, clumsy ferry-boats, their decks swarming with people, railroad transports carrying lines of brown, blue and white freight cars, stately sound steamers, dclass tramp steamers, coasters, dredgers, scows, and everywhere pervading the entire bay impudent little tugs puffing and whistling officiously;these were the craft which churned the sunlight waters as far as the eye could reach. In calm contrast to the hurry of sailing vessel and steamer a silent fleet of white warships lay motionless in midstream.\n' +
        '\n' +
        'Constance\'s merry laugh aroused me from my reverie.\n' +
        '\n' +
        '"What are you staring at?" she inquired.\n' +
        '\n' +
        '"Nothingthe fleet," I smiled.\n' +
        '\n' +
        'Then Louis told us what the vessels were, pointing out each by its relative position to the old Red Fort on Governor\'s Island.\n' +
        '\n' +
        '"That little cigar shaped thing is a torpedo boat," he explained; "there are four more lying close together. They are the Tarpon, the Falcon, the Sea Fox, and the Octopus. The gun-boats just above are the Princeton, the Champlain, the Still Water and the Erie. Next to them lie the cruisers Faragut and Los Angeles, and above them the battle ships California, and Dakota, and the Washington which is the flag ship. Those two squatty looking chunks of metal which are anchored there off Castle William are the double turreted monitors Terrible and Magnificent; behind them lies the ram, Osceola."\n' +
        '\n' +
        'Constance looked at him with deep approval in her beautiful eyes. "What loads of things you know for a soldier," she said, and we all joined in the laugh which followed.\n' +
        '\n' +
        'Presently Louis rose with a nod to us and offered his arm to Constance, and they strolled away along the river wall. Hawberk watched them for a moment and then turned to me.\n' +
        '\n' +
        '"Mr. Wilde was right," he said. "I have found the missing tassets and left cuissard of the \'Prince\'s Emblazoned,\' in a vile old junk garret in Pell Street."\n' +
        '\n' +
        '"998?" I inquired, with a smile.\n' +
        '\n' +
        '"Yes."\n' +
        '\n' +
        '"Mr. Wilde is a very intelligent man," I observed.\n' +
        '\n' +
        '"I want to give him the credit of this most important discovery," continued Hawberk. "And I intend it shall be known that he is entitled to the fame of it."\n' +
        '\n' +
        '"He won\'t thank you for that," I answered sharply; "please say nothing about it."\n' +
        '\n' +
        '"Do you know what it is worth?" said Hawberk.\n' +
        '\n' +
        '"No, fifty dollars, perhaps."\n' +
        '\n' +
        '"It is valued at five hundred, but the owner of the \'Prince\'s Emblazoned\' will give two thousand dollars to the person who completes his suit; that reward also belongs to Mr. Wilde."\n' +
        '\n' +
        '"He doesn\'t want it! He refuses it!" I answered angrily. "What do you know about Mr. Wilde? He doesn\'t need the money. He is richor will bericher than any living man except myself. What will we care for money thenwhat will we care, he and I, whenwhen"\n' +
        '\n' +
        '"When what?" demanded Hawberk, astonished.\n' +
        '\n' +
        '"You will see," I replied, on my guard again.\n' +
        '\n' +
        'He looked at me narrowly, much as Doctor Archer used to, and I knew he thought I was mentally unsound. Perhaps it was fortunate for him that he did not use the word lunatic just then.\n' +
        '\n' +
        '"No," I replied to his unspoken thought, "I am not mentally weak; my mind is as healthy as Mr. Wilde\'s. I do not care to explain just yet what I have on hand, but it is an investment which will pay more than mere gold, silver and precious stones. It will secure the happiness and prosperity of a continentyes, a hemisphere!"\n' +
        '\n' +
        '"Oh," said Hawberk.\n' +
        '\n' +
        '"And eventually," I continued more quietly, "it will secure the happiness of the whole world."\n' +
        '\n' +
        '"And incidentally your own happiness and prosperity as well as Mr. Wilde\'s?"\n' +
        '\n' +
        '"Exactly," I smiled. But I could have throttled him for taking that tone.\n' +
        '\n' +
        'He looked at me in silence for a while and then said very gently, "Why don\'t you give up your books and studies, Mr. Castaigne, and take a tramp among the mountains somewhere or other? You used to be fond of fishing. Take a cast or two at the trout in the Rangelys."\n' +
        '\n' +
        '"I don\'t care for fishing any more," I answered, without a shade of annoyance in my voice.\n' +
        '\n' +
        '"You used to be fond of everything," he continued; "athletics, yachting, shooting, riding"\n' +
        '\n' +
        '"I have never cared to ride since my fall," I said quietly.\n' +
        '\n' +
        '"Ah, yes, your fall," he repeated, looking away from me.\n' +
        '\n' +
        'I thought this nonsense had gone far enough, so I brought the conversation back to Mr. Wilde; but he was scanning my face again in a manner highly offensive to me.\n' +
        '\n' +
        '"Mr. Wilde," he repeated, "do you know what he did this afternoon? He came downstairs and nailed a sign over the hall door next to mine; it read:\n' +
        '\n' +
        'MR. WILDE,\n' +
        'REPAIRER OF REPUTATIONS.\n' +
        'Third Bell.\n' +
        '\n' +
        '"Do you know what a Repairer of Reputations can be?"\n' +
        '\n' +
        '"I do," I replied, suppressing the rage within.\n' +
        '\n' +
        '"Oh," he said again.\n' +
        '\n' +
        'Louis and Constance came strolling by and stopped to ask if we would join them. Hawberk looked at his watch. At the same moment a puff of smoke shot from the casemates of Castle William, and the boom of the sunset gun rolled across the water and was re-echoed from the Highlands opposite. The flag came running down from the flag-pole, the bugles sounded on the white decks of the warships, and the first electric light sparkled out from the Jersey shore.\n' +
        '\n' +
        'As I turned into the city with Hawberk I heard Constance murmur something to Louis which I did not understand; but Louis whispered "My darling," in reply; and again, walking ahead with Hawberk through the square I heard a murmur of "sweetheart," and "my own Constance," and I knew the time had nearly arrived when I should speak of important matters with my cousin Louis.\n' +
        'III\n' +
        '\n' +
        'One morning early in May I stood before the steel safe in my bedroom, trying on the golden jewelled crown. The diamonds flashed fire as I turned to the mirror, and the heavy beaten gold burned like a halo about my head. I remembered Camilla\'s agonized scream and the awful words echoing through the dim streets of Carcosa. They were the last lines in the first act, and I dared not think of what followeddared not, even in the spring sunshine, there in my own room, surrounded with familiar objects, reassured by the bustle from the street and the voices of the servants in the hallway outside. For those poisoned words had dropped slowly into my heart, as death-sweat drops upon a bed-sheet and is absorbed. Trembling, I put the diadem from my head and wiped my forehead, but I thought of Hastur and of my own rightful ambition, and I remembered Mr. Wilde as I had last left him, his face all torn and bloody from the claws of that devil\'s creature, and what he saidah, what he said. The alarm bell in the safe began to whirr harshly, and I knew my time was up; but I would not heed it, and replacing the flashing circlet upon my head I turned defiantly to the mirror. I stood for a long time absorbed in the changing expression of my own eyes. The mirror reflected a face which was like my own, but whiter, and so thin that I hardly recognized it. And all the time I kept repeating between my clenched teeth, "The day has come! the day has come!" while the alarm in the safe whirred and clamoured, and the diamonds sparkled and flamed above my brow. I heard a door open but did not heed it. It was only when I saw two faces in the mirror:it was only when another face rose over my shoulder, and two other eyes met mine. I wheeled like a flash and seized a long knife from my dressing-table, and my cousin sprang back very pale, crying: "Hildred! for God\'s sake!" then as my hand fell, he said: "It is I, Louis, don\'t you know me?" I stood silent. I could not have spoken for my life. He walked up to me and took the knife from my hand.\n' +
        '\n' +
        '"What is all this?" he inquired, in a gentle voice. "Are you ill?"\n' +
        '\n' +
        '"No," I replied. But I doubt if he heard me.\n' +
        '\n' +
        '"Come, come, old fellow," he cried, "take off that brass crown and toddle into the study. Are you going to a masquerade? What\'s all this theatrical tinsel anyway?"\n' +
        '\n' +
        'I was glad he thought the crown was made of brass and paste, yet I didn\'t like him any the better for thinking so. I let him take it from my hand, knowing it was best to humour him. He tossed the splendid diadem in the air, and catching it, turned to me smiling.\n' +
        '\n' +
        '"It\'s dear at fifty cents," he said. "What\'s it for?"\n' +
        '\n' +
        'I did not answer, but took the circlet from his hands, and placing it in the safe shut the massive steel door. The alarm ceased its infernal din at once. He watched me curiously, but did not seem to notice the sudden ceasing of the alarm. He did, however, speak of the safe as a biscuit box. Fearing lest he might examine the combination I led the way into my study. Louis threw himself on the sofa and flicked at flies with his eternal riding-whip. He wore his fatigue uniform with the braided jacket and jaunty cap, and I noticed that his riding-boots were all splashed with red mud.\n' +
        '\n' +
        '"Where have you been?" I inquired.\n' +
        '\n' +
        '"Jumping mud creeks in Jersey," he said. "I haven\'t had time to change yet; I was rather in a hurry to see you. Haven\'t you got a glass of something? I\'m dead tired; been in the saddle twenty-four hours."\n' +
        '\n' +
        'I gave him some brandy from my medicinal store, which he drank with a grimace.\n' +
        '\n' +
        '"Damned bad stuff," he observed. "I\'ll give you an address where they sell brandy that is brandy."\n' +
        '\n' +
        '"It\'s good enough for my needs," I said indifferently. "I use it to rub my chest with." He stared and flicked at another fly.\n' +
        '\n' +
        '"See here, old fellow," he began, "I\'ve got something to suggest to you. It\'s four years now that you\'ve shut yourself up here like an owl, never going anywhere, never taking any healthy exercise, never doing a damn thing but poring over those books up there on the mantelpiece."\n' +
        '\n' +
        'He glanced along the row of shelves. "Napoleon, Napoleon, Napoleon!" he read. "For heaven\'s sake, have you nothing but Napoleons there?"\n' +
        '\n' +
        '"I wish they were bound in gold," I said. "But wait, yes, there is another book, The King in Yellow." I looked him steadily in the eye.\n' +
        '\n' +
        '"Have you never read it?" I asked.\n' +
        '\n' +
        '"I? No, thank God! I don\'t want to be driven crazy."\n' +
        '\n' +
        'I saw he regretted his speech as soon as he had uttered it. There is only one word which I loathe more than I do lunatic and that word is crazy. But I controlled myself and asked him why he thought The King in Yellow dangerous.\n' +
        '\n' +
        '"Oh, I don\'t know," he said, hastily. "I only remember the excitement it created and the denunciations from pulpit and Press. I believe the author shot himself after bringing forth this monstrosity, didn\'t he?"\n' +
        '\n' +
        '"I understand he is still alive," I answered.\n' +
        '\n' +
        '"That\'s probably true," he muttered; "bullets couldn\'t kill a fiend like that."\n' +
        '\n' +
        '"It is a book of great truths," I said.\n' +
        '\n' +
        '"Yes," he replied, "of \'truths\' which send men frantic and blast their lives. I don\'t care if the thing is, as they say, the very supreme essence of art. It\'s a crime to have written it, and I for one shall never open its pages."\n' +
        '\n' +
        '"Is that what you have come to tell me?" I asked.\n' +
        '\n' +
        '"No," he said, "I came to tell you that I am going to be married."\n' +
        '\n' +
        'I believe for a moment my heart ceased to beat, but I kept my eyes on his face.\n' +
        '\n' +
        '"Yes," he continued, smiling happily, "married to the sweetest girl on earth."\n' +
        '\n' +
        '"Constance Hawberk," I said mechanically.\n' +
        '\n' +
        '"How did you know?" he cried, astonished. "I didn\'t know it myself until that evening last April, when we strolled down to the embankment before dinner."\n' +
        '\n' +
        '"When is it to be?" I asked.\n' +
        '\n' +
        '"It was to have been next September, but an hour ago a despatch came ordering our regiment to the Presidio, San Francisco. We leave at noon to-morrow. To-morrow," he repeated. "Just think, Hildred, to-morrow I shall be the happiest fellow that ever drew breath in this jolly world, for Constance will go with me."\n' +
        '\n' +
        'I offered him my hand in congratulation, and he seized and shook it like the good-natured fool he wasor pretended to be.\n' +
        '\n' +
        '"I am going to get my squadron as a wedding present," he rattled on. "Captain and Mrs. Louis Castaigne, eh, Hildred?"\n' +
        '\n' +
        'Then he told me where it was to be and who were to be there, and made me promise to come and be best man. I set my teeth and listened to his boyish chatter without showing what I felt, but\n' +
        '\n' +
        'I was getting to the limit of my endurance, and when he jumped up, and, switching his spurs till they jingled, said he must go, I did not detain him.\n' +
        '\n' +
        '"There\'s one thing I want to ask of you," I said quietly.\n' +
        '\n' +
        '"Out with it, it\'s promised," he laughed.\n' +
        '\n' +
        '"I want you to meet me for a quarter of an hour\'s talk to-night."\n' +
        '\n' +
        '"Of course, if you wish," he said, somewhat puzzled. "Where?"\n' +
        '\n' +
        '"Anywhere, in the park there."\n' +
        '\n' +
        '"What time, Hildred?"\n' +
        '\n' +
        '"Midnight."\n' +
        '\n' +
        '"What in the name of" he began, but checked himself and laughingly assented. I watched him go down the stairs and hurry away, his sabre banging at every stride. He turned into Bleecker Street, and I knew he was going to see Constance. I gave him ten minutes to disappear and then followed in his footsteps, taking with me the jewelled crown and the silken robe embroidered with the Yellow Sign. When I turned into Bleecker Street, and entered the doorway which bore the sign\n' +
        '\n' +
        'MR. WILDE,\n' +
        'REPAIRER OF REPUTATIONS.\n' +
        'Third Bell.\n' +
        '\n' +
        'I saw old Hawberk moving about in his shop, and imagined I heard Constance\'s voice in the parlour; but I avoided them both and hurried up the trembling stairways to Mr. Wilde\'s apartment. I knocked and entered without ceremony. Mr. Wilde lay groaning on the floor, his face covered with blood, his clothes torn to shreds. Drops of blood were scattered about over the carpet, which had also been ripped and frayed in the evidently recent struggle.\n' +
        '\n' +
        '"It\'s that cursed cat," he said, ceasing his groans, and turning his colourless eyes to me; "she attacked me while I was asleep. I believe she will kill me yet."\n' +
        '\n' +
        'This was too much, so I went into the kitchen, and, seizing a hatchet from the pantry, started to find the infernal beast and settle her then and there. My search was fruitless, and after a while I gave it up and came back to find Mr. Wilde squatting on his high chair by the table. He had washed his face and changed his clothes. The great furrows which the cat\'s claws had ploughed up in his face he had filled with collodion, and a rag hid the wound in his throat. I told him I should kill the cat when I came across her, but he only shook his head and turned to the open ledger before him. He read name after name of the people who had come to him in regard to their reputation, and the sums he had amassed were startling.\n' +
        '\n' +
        '"I put on the screws now and then," he explained.\n' +
        '\n' +
        '"One day or other some of these people will assassinate you," I insisted.\n' +
        '\n' +
        '"Do you think so?" he said, rubbing his mutilated ears.\n' +
        '\n' +
        'It was useless to argue with him, so I took down the manuscript entitled Imperial Dynasty of America, for the last time I should ever take it down in Mr. Wilde\'s study. I read it through, thrilling and trembling with pleasure. When I had finished Mr. Wilde took the manuscript and, turning to the dark passage which leads from his study to his bed-chamber, called out in a loud voice, "Vance." Then for the first time, I noticed a man crouching there in the shadow. How I had overlooked him during my search for the cat, I cannot imagine.\n' +
        '\n' +
        '"Vance, come in," cried Mr. Wilde.\n' +
        '\n' +
        'The figure rose and crept towards us, and I shall never forget the face that he raised to mine, as the light from the window illuminated it.\n' +
        '\n' +
        '"Vance, this is Mr. Castaigne," said Mr. Wilde. Before he had finished speaking, the man threw himself on the ground before the table, crying and grasping, "Oh, God! Oh, my God! Help me! Forgive me! Oh, Mr. Castaigne, keep that man away. You cannot, you cannot mean it! You are differentsave me! I am broken downI was in a madhouse and nowwhen all was coming rightwhen I had forgotten the Kingthe King in Yellow andbut I shall go mad againI shall go mad"\n' +
        '\n' +
        'His voice died into a choking rattle, for Mr. Wilde had leapt on him and his right hand encircled the man\'s throat. When Vance fell in a heap on the floor, Mr. Wilde clambered nimbly into his chair again, and rubbing his mangled ears with the stump of his hand, turned to me and asked me for the ledger. I reached it down from the shelf and he opened it. After a moment\'s searching among the beautifully written pages, he coughed complacently, and pointed to the name Vance.\n' +
        '\n' +
        '"Vance," he read aloud, "Osgood Oswald Vance." At the sound of his name, the man on the floor raised his head and turned a convulsed face to Mr. Wilde. His eyes were injected with blood, his lips tumefied. "Called April 28th," continued Mr. Wilde. "Occupation, cashier in the Seaforth National Bank; has served a term of forgery at Sing Sing, from whence he was transferred to the Asylum for the Criminal Insane. Pardoned by the Governor of New York, and discharged from the Asylum, January 19, 1918. Reputation damaged at Sheepshead Bay. Rumours that he lives beyond his income. Reputation to be repaired at once. Retainer $1,500.\n' +
        '\n' +
        '"Note.Has embezzled sums amounting to $30,000 since March 20, 1919, excellent family, and secured present position through uncle\'s influence. Father, President of Seaforth Bank."\n' +
        '\n' +
        'I looked at the man on the floor.\n' +
        '\n' +
        '"Get up, Vance," said Mr. Wilde in a gentle voice. Vance rose as if hypnotized. "He will do as we suggest now," observed Mr. Wilde, and opening the manuscript, he read the entire history of the Imperial Dynasty of America. Then in a kind and soothing murmur he ran over the important points with Vance, who stood like one stunned. His eyes were so blank and vacant that I imagined he had become half-witted, and remarked it to Mr. Wilde who replied that it was of no consequence anyway. Very patiently we pointed out to Vance what his share in the affair would be, and he seemed to understand after a while. Mr. Wilde explained the manuscript, using several volumes on Heraldry, to substantiate the result of his researches. He mentioned the establishment of the Dynasty in Carcosa, the lakes which connected Hastur, Aldebaran and the mystery of the Hyades. He spoke of Cassilda and Camilla, and sounded the cloudy depths of Demhe, and the Lake of Hali. "The scolloped tatters of the King in Yellow must hide Yhtill forever," he muttered, but I do not believe Vance heard him. Then by degrees he led Vance along the ramifications of the Imperial family, to Uoht and Thale, from Naotalba and Phantom of Truth, to Aldones, and then tossing aside his manuscript and notes, he began the wonderful story of the Last King. Fascinated and thrilled I watched him. He threw up his head, his long arms were stretched out in a magnificent gesture of pride and power, and his eyes blazed deep in their sockets like two emeralds. Vance listened stupefied. As for me, when at last Mr. Wilde had finished, and pointing to me, cried, "The cousin of the King!" my head swam with excitement.\n' +
        '\n' +
        'Controlling myself with a superhuman effort, I explained to Vance why I alone was worthy of the crown and why my cousin must be exiled or die. I made him understand that my cousin must never marry, even after renouncing all his claims, and how that least of all he should marry the daughter of the Marquis of Avonshire and bring England into the question. I showed him a list of thousands of names which Mr. Wilde had drawn up; every man whose name was there had received the Yellow Sign which no living human being dared disregard. The city, the state, the whole land, were ready to rise and tremble before the Pallid Mask.\n' +
        '\n' +
        'The time had come, the people should know the son of Hastur, and the whole world bow to the black stars which hang in the sky over Carcosa.\n' +
        '\n' +
        'Vance leaned on the table, his head buried in his hands. Mr. Wilde drew a rough sketch on the margin of yesterday\'s Herald with a bit of lead pencil. It was a plan of Hawberk\'s rooms. Then he wrote out the order and affixed the seal, and shaking like a palsied man I signed my first writ of execution with my name Hildred-Rex.\n' +
        '\n' +
        'Mr. Wilde clambered to the floor and unlocking the cabinet, took a long square box from the first shelf. This he brought to the table and opened. A new knife lay in the tissue paper inside and I picked it up and handed it to Vance, along with the order and the plan of Hawberk\'s apartment. Then Mr. Wilde told Vance he could go; and he went, shambling like an outcast of the slums.\n' +
        '\n' +
        'I sat for a while watching the daylight fade behind the square tower of the Judson Memorial Church, and finally, gathering up the manuscript and notes, took my hat and started for the door.\n' +
        '\n' +
        'Mr. Wilde watched me in silence. When I had stepped into the hall I looked back. Mr. Wilde\'s small eyes were still fixed on me. Behind him, the shadows gathered in the fading light. Then I closed the door behind me and went out into the darkening streets.\n' +
        '\n' +
        'I had eaten nothing since breakfast, but I was not hungry. A wretched, half-starved creature, who stood looking across the street at the Lethal Chamber, noticed me and came up to tell me a tale of misery. I gave him money, I don\'t know why, and he went away without thanking me. An hour later another outcast approached and whined his story. I had a blank bit of paper in my pocket, on which was traced the Yellow Sign, and I handed it to him. He looked at it stupidly for a moment, and then with an uncertain glance at me, folded it with what seemed to me exaggerated care and placed it in his bosom.\n' +
        '\n' +
        'The electric lights were sparkling among the trees, and the new moon shone in the sky above the Lethal Chamber. It was tiresome waiting in the square; I wandered from the Marble Arch to the artillery stables and back again to the lotos fountain. The flowers and grass exhaled a fragrance which troubled me. The jet of the fountain played in the moonlight, and the musical splash of falling drops reminded me of the tinkle of chained mail in Hawberk\'s shop. But it was not so fascinating, and the dull sparkle of the moonlight on the water brought no such sensations of exquisite pleasure, as when the sunshine played over the polished steel of a corselet on Hawberk\'s knee. I watched the bats darting and turning above the water plants in the fountain basin, but their rapid, jerky flight set my nerves on edge, and I went away again to walk aimlessly to and fro among the trees.\n' +
        '\n' +
        'The artillery stables were dark, but in the cavalry barracks the officers\' windows were brilliantly lighted, and the sallyport was constantly filled with troopers in fatigue, carrying straw and harness and baskets filled with tin dishes.\n' +
        '\n' +
        'Twice the mounted sentry at the gates was changed while I wandered up and down the asphalt walk. I looked at my watch. It was nearly time. The lights in the barracks went out one by one, the barred gate was closed, and every minute or two an officer passed in through the side wicket, leaving a rattle of accoutrements and a jingle of spurs on the night air. The square had become very silent. The last homeless loiterer had been driven away by the grey-coated park policeman, the car tracks along Wooster Street were deserted, and the only sound which broke the stillness was the stamping of the sentry\'s horse and the ring of his sabre against the saddle pommel. In the barracks, the officers\' quarters were still lighted, and military servants passed and repassed before the bay windows. Twelve o\'clock sounded from the new spire of St. Francis Xavier, and at the last stroke of the sad-toned bell a figure passed through the wicket beside the portcullis, returned the salute of the sentry, and crossing the street entered the square and advanced toward the Benedick apartment house.\n' +
        '\n' +
        '"Louis," I called.\n' +
        '\n' +
        'The man pivoted on his spurred heels and came straight toward me.\n' +
        '\n' +
        '"Is that you, Hildred?"\n' +
        '\n' +
        '"Yes, you are on time."\n' +
        '\n' +
        'I took his offered hand, and we strolled toward the Lethal Chamber.\n' +
        '\n' +
        'He rattled on about his wedding and the graces of Constance, and their future prospects, calling my attention to his captain\'s shoulder-straps, and the triple gold arabesque on his sleeve and fatigue cap. I believe I listened as much to the music of his spurs and sabre as I did to his boyish babble, and at last we stood under the elms on the Fourth Street corner of the square opposite the Lethal Chamber. Then he laughed and asked me what I wanted with him. I motioned him to a seat on a bench under the electric light, and sat down beside him. He looked at me curiously, with that same searching glance which I hate and fear so in doctors. I felt the insult of his look, but he did not know it, and I carefully concealed my feelings.\n' +
        '\n' +
        '"Well, old chap," he inquired, "what can I do for you?"\n' +
        '\n' +
        'I drew from my pocket the manuscript and notes of the Imperial Dynasty of America, and looking him in the eye said:\n' +
        '\n' +
        '"I will tell you. On your word as a soldier, promise me to read this manuscript from beginning to end, without asking me a question. Promise me to read these notes in the same way, and promise me to listen to what I have to tell later."\n' +
        '\n' +
        '"I promise, if you wish it," he said pleasantly. "Give me the paper, Hildred."\n' +
        '\n' +
        'He began to read, raising his eyebrows with a puzzled, whimsical air, which made me tremble with suppressed anger. As he advanced his, eyebrows contracted, and his lips seemed to form the word "rubbish."\n' +
        '\n' +
        'Then he looked slightly bored, but apparently for my sake read, with an attempt at interest, which presently ceased to be an effort. He started when in the closely written pages he came to his own name, and when he came to mine he lowered the paper, and looked sharply at me for a moment. But he kept his word, and resumed his reading, and I let the half-formed question die on his lips unanswered. When he came to the end and read the signature of Mr. Wilde, he folded the paper carefully and returned it to me. I handed him the notes, and he settled back, pushing his fatigue cap up to his forehead, with a boyish gesture, which I remembered so well in school. I watched his face as he read, and when he finished I took the notes with the manuscript, and placed them in my pocket. Then I unfolded a scroll marked with the Yellow Sign. He saw the sign, but he did not seem to recognize it, and I called his attention to it somewhat sharply.\n' +
        '\n' +
        '"Well," he said, "I see it. What is it?"\n' +
        '\n' +
        '"It is the Yellow Sign," I said angrily.\n' +
        '\n' +
        '"Oh, that\'s it, is it?" said Louis, in that flattering voice, which Doctor Archer used to employ with me, and would probably have employed again, had I not settled his affair for him.\n' +
        '\n' +
        'I kept my rage down and answered as steadily as possible, "Listen, you have engaged your word?"\n' +
        '\n' +
        '"I am listening, old chap," he replied soothingly.\n' +
        '\n' +
        'I began to speak very calmly.\n' +
        '\n' +
        '"Dr. Archer, having by some means become possessed of the secret of the Imperial Succession, attempted to deprive me of my right, alleging that because of a fall from my horse four years ago, I had become mentally deficient. He presumed to place me under restraint in his own house in hopes of either driving me insane or poisoning me. I have not forgotten it. I visited him last night and the interview was final."\n' +
        '\n' +
        'Louis turned quite pale, but did not move. I resumed triumphantly, "There are yet three people to be interviewed in the interests of Mr. Wilde and myself. They are my cousin Louis, Mr. Hawberk, and his daughter Constance."\n' +
        '\n' +
        'Louis sprang to his feet and I arose also, and flung the paper marked with the Yellow Sign to the ground.\n' +
        '\n' +
        '"Oh, I don\'t need that to tell you what I have to say," I cried, with a laugh of triumph. "You must renounce the crown to me, do you hear, to me."\n' +
        '\n' +
        'Louis looked at me with a startled air, but recovering himself said kindly, "Of course I renounce thewhat is it I must renounce?"\n' +
        '\n' +
        '"The crown," I said angrily.\n' +
        '\n' +
        '"Of course," he answered, "I renounce it. Come, old chap, I\'ll walk back to your rooms with you."\n' +
        '\n' +
        '"Don\'t try any of your doctor\'s tricks on me," I cried, trembling with fury. "Don\'t act as if you think I am insane."\n' +
        '\n' +
        '"What nonsense," he replied. "Come, it\'s getting late, Hildred."\n' +
        '\n' +
        '"No," I shouted, "you must listen. You cannot marry, I forbid it. Do you hear? I forbid it. You shall renounce the crown, and in reward I grant you exile, but if you refuse you shall die."\n' +
        '\n' +
        'He tried to calm me, but I was roused at last, and drawing my long knife barred his way.\n' +
        '\n' +
        'Then I told him how they would find Dr. Archer in the cellar with his throat open, and I laughed in his face when I thought of Vance and his knife, and the order signed by me.\n' +
        '\n' +
        '"Ah, you are the King," I cried, "but I shall be King. Who are you to keep me from Empire over all the habitable earth! I was born the cousin of a king, but I shall be King!"\n' +
        '\n' +
        'Louis stood white and rigid before me. Suddenly a man came running up Fourth Street, entered the gate of the Lethal Temple, traversed the path to the bronze doors at full speed, and plunged into the death chamber with the cry of one demented, and I laughed until I wept tears, for I had recognized Vance, and knew that Hawberk and his daughter were no longer in my way.\n' +
        '\n' +
        '"Go," I cried to Louis, "you have ceased to be a menace. You will never marry Constance now, and if you marry any one else in your exile, I will visit you as I did my doctor last night. Mr. Wilde takes charge of you to-morrow." Then I turned and darted into South Fifth Avenue, and with a cry of terror Louis dropped his belt and sabre and followed me like the wind. I heard him close behind me at the corner of Bleecker Street, and I dashed into the doorway under Hawberk\'s sign. He cried, "Halt, or I fire!" but when he saw that I flew up the stairs leaving Hawberk\'s shop below, he left me, and I heard him hammering and shouting at their door as though it were possible to arouse the dead.\n' +
        '\n' +
        'Mr. Wilde\'s door was open, and I entered crying, "It is done, it is done! Let the nations rise and look upon their King!" but I could not find Mr. Wilde, so I went to the cabinet and took the splendid diadem from its case. Then I drew on the white silk robe, embroidered with the Yellow Sign, and placed the crown upon my head. At last I was King, King by my right in Hastur, King because I knew the mystery of the Hyades, and my mind had sounded the depths of the Lake of Hali. I was King! The first grey pencillings of dawn would raise a tempest which would shake two hemispheres. Then as I stood, my every nerve pitched to the highest tension, faint with the joy and splendour of my thought, without, in the dark passage, a man groaned.\n' +
        '\n' +
        'I seized the tallow dip and sprang to the door. The cat passed me like a demon, and the tallow dip went out, but my long knife flew swifter than she, and I heard her screech, and I knew that my knife had found her. For a moment I listened to her tumbling and thumping about in the darkness, and then when her frenzy ceased, I lighted a lamp and raised it over my head. Mr. Wilde lay on the floor with his throat torn open. At first I thought he was dead, but as I looked, a green sparkle came into his sunken eyes, his mutilated hand trembled, and then a spasm stretched his mouth from ear to ear. For a moment my terror and despair gave place to hope, but as I bent over him his eyeballs rolled clean around in his head, and he died. Then while I stood, transfixed with rage and despair, seeing my crown, my empire, every hope and every ambition, my very life, lying prostrate there with the dead master, they came, seized me from behind, and bound me until my veins stood out like cords, and my voice failed with the paroxysms of my frenzied screams. But I still raged, bleeding and infuriated among them, and more than one policeman felt my sharp teeth. Then when I could no longer move they came nearer; I saw old Hawberk, and behind him my cousin Louis\' ghastly face, and farther away, in the corner, a woman, Constance, weeping softly.\n' +
        '\n' +
        '"Ah! I see it now!" I shrieked. "You have seized the throne and the empire. Woe! woe to you who are crowned with the crown of the King in Yellow!"\n' +
        '\n' +
        '[EDITOR\'S NOTE.Mr. Castaigne died yesterday in the Asylum for Criminal Insane.]\n' +
        'THE MASK\n' +
        'Camilla: You, sir, should unmask.\n' +
        'Stranger: Indeed?\n' +
        'Cassilda: Indeed it\'s time. We all have laid aside disguise but you.\n' +
        'Stranger: I wear no mask.\n' +
        'Camilla: (Terrified, aside to Cassilda.) No mask? No mask!\n' +
        'The King in Yellow, Act I, Scene 2.\n' +
        'I\n' +
        '\n' +
        'Although I knew nothing of chemistry, I listened fascinated. He picked up an Easter lily which Genevive had brought that morning from Notre Dame, and dropped it into the basin. Instantly the liquid lost its crystalline clearness. For a second the lily was enveloped in a milk-white foam, which disappeared, leaving the fluid opalescent. Changing tints of orange and crimson played over the surface, and then what seemed to be a ray of pure sunlight struck through from the bottom where the lily was resting. At the same instant he plunged his hand into the basin and drew out the flower. "There is no danger," he explained, "if you choose the right moment. That golden ray is the signal."\n' +
        '\n' +
        'He held the lily toward me, and I took it in my hand. It had turned to stone, to the purest marble.\n' +
        '\n' +
        '"You see," he said, "it is without a flaw. What sculptor could reproduce it?"\n' +
        '\n' +
        'The marble was white as snow, but in its depths the veins of the lily were tinged with palest azure, and a faint flush lingered deep in its heart.\n' +
        '\n' +
        '"Don\'t ask me the reason of that," he smiled, noticing my wonder. "I have no idea why the veins and heart are tinted, but they always are. Yesterday I tried one of Genevive\'s gold-fish,there it is."\n' +
        '\n' +
        'The fish looked as if sculptured in marble. But if you held it to the light the stone was beautifully veined with a faint blue, and from somewhere within came a rosy light like the tint which slumbers in an opal. I looked into the basin. Once more it seemed filled with clearest crystal.\n' +
        '\n' +
        '"If I should touch it now?" I demanded.\n' +
        '\n' +
        '"I don\'t know," he replied, "but you had better not try."\n' +
        '\n' +
        '"There is one thing I\'m curious about," I said, "and that is where the ray of sunlight came from."\n' +
        '\n' +
        '"It looked like a sunbeam true enough," he said. "I don\'t know, it always comes when I immerse any living thing. Perhaps," he continued, smiling, "perhaps it is the vital spark of the creature escaping to the source from whence it came."\n' +
        '\n' +
        'I saw he was mocking, and threatened him with a mahl-stick, but he only laughed and changed the subject.\n' +
        '\n' +
        '"Stay to lunch. Genevive will be here directly."\n' +
        '\n' +
        '"I saw her going to early mass," I said, "and she looked as fresh and sweet as that lilybefore you destroyed it."\n' +
        '\n' +
        '"Do you think I destroyed it?" said Boris gravely.\n' +
        '\n' +
        '"Destroyed, preserved, how can we tell?"\n' +
        '\n' +
        'We sat in the corner of a studio near his unfinished group of the "Fates." He leaned back on the sofa, twirling a sculptor\'s chisel and squinting at his work.\n' +
        '\n' +
        '"By the way," he said, "I have finished pointing up that old academic Ariadne, and I suppose it will have to go to the Salon. It\'s all I have ready this year, but after the success the \'Madonna\' brought me I feel ashamed to send a thing like that."\n' +
        '\n' +
        'The "Madonna," an exquisite marble for which Genevive had sat, had been the sensation of last year\'s Salon. I looked at the Ariadne. It was a magnificent piece of technical work, but I agreed with Boris that the world would expect something better of him than that. Still, it was impossible now to think of finishing in time for the Salon that splendid terrible group half shrouded in the marble behind me. The "Fates" would have to wait.\n' +
        '\n' +
        'We were proud of Boris Yvain. We claimed him and he claimed us on the strength of his having been born in America, although his father was French and his mother was a Russian. Every one in the Beaux Arts called him Boris. And yet there were only two of us whom he addressed in the same familiar wayJack Scott and myself.\n' +
        '\n' +
        'Perhaps my being in love with Genevive had something to do with his affection for me. Not that it had ever been acknowledged between us. But after all was settled, and she had told me with tears in her eyes that it was Boris whom she loved, I went over to his house and congratulated him. The perfect cordiality of that interview did not deceive either of us, I always believed, although to one at least it was a great comfort. I do not think he and Genevive ever spoke of the matter together, but Boris knew.\n' +
        '\n' +
        'Genevive was lovely. The Madonna-like purity of her face might have been inspired by the Sanctus in Gounod\'s Mass. But I was always glad when she changed that mood for what we called her "April Manuvres." She was often as variable as an April day. In the morning grave, dignified and sweet, at noon laughing, capricious, at evening whatever one least expected. I preferred her so rather than in that Madonna-like tranquillity which stirred the depths of my heart. I was dreaming of Genevive when he spoke again.\n' +
        '\n' +
        '"What do you think of my discovery, Alec?"\n' +
        '\n' +
        '"I think it wonderful."\n' +
        '\n' +
        '"I shall make no use of it, you know, beyond satisfying my own curiosity so far as may be, and the secret will die with me."\n' +
        '\n' +
        '"It would be rather a blow to sculpture, would it not? We painters lose more than we ever gain by photography."\n' +
        '\n' +
        'Boris nodded, playing with the edge of the chisel.\n' +
        '\n' +
        '"This new vicious discovery would corrupt the world of art. No, I shall never confide the secret to any one," he said slowly.\n' +
        '\n' +
        'It would be hard to find any one less informed about such phenomena than myself; but of course I had heard of mineral springs so saturated with silica that the leaves and twigs which fell into them were turned to stone after a time. I dimly comprehended the process, how the silica replaced the vegetable matter, atom by atom, and the result was a duplicate of the object in stone. This, I confess, had never interested me greatly, and as for the ancient fossils thus produced, they disgusted me. Boris, it appeared, feeling curiosity instead of repugnance, had investigated the subject, and had accidentally stumbled on a solution which, attacking the immersed object with a ferocity unheard of, in a second did the work of years. This was all I could make out of the strange story he had just been telling me. He spoke again after a long silence.\n' +
        '\n' +
        '"I am almost frightened when I think what I have found. Scientists would go mad over the discovery. It was so simple too; it discovered itself. When I think of that formula, and that new element precipitated in metallic scales"\n' +
        '\n' +
        '"What new element?"\n' +
        '\n' +
        '"Oh, I haven\'t thought of naming it, and I don\'t believe I ever shall. There are enough precious metals now in the world to cut throats over."\n' +
        '\n' +
        'I pricked up my ears. "Have you struck gold, Boris?"\n' +
        '\n' +
        '"No, better;but see here, Alec!" he laughed, starting up. "You and I have all we need in this world. Ah! how sinister and covetous you look already!" I laughed too, and told him I was devoured by the desire for gold, and we had better talk of something else; so when Genevive came in shortly after, we had turned our backs on alchemy.\n' +
        '\n' +
        'Genevive was dressed in silvery grey from head to foot. The light glinted along the soft curves of her fair hair as she turned her cheek to Boris; then she saw me and returned my greeting. She had never before failed to blow me a kiss from the tips of her white fingers, and I promptly complained of the omission. She smiled and held out her hand, which dropped almost before it had touched mine; then she said, looking at Boris\n' +
        '\n' +
        '"You must ask Alec to stay for luncheon." This also was something new. She had always asked me herself until to-day.\n' +
        '\n' +
        '"I did," said Boris shortly.\n' +
        '\n' +
        '"And you said yes, I hope?" She turned to me with a charming conventional smile. I might have been an acquaintance of the day before yesterday. I made her a low bow. "J\'avais bien l\'honneur, madame," but refusing to take up our usual bantering tone, she murmured a hospitable commonplace and disappeared. Boris and I looked at one another.\n' +
        '\n' +
        '"I had better go home, don\'t you think?" I asked.\n' +
        '\n' +
        '"Hanged if I know," he replied frankly.\n' +
        '\n' +
        'While we were discussing the advisability of my departure Genevive reappeared in the doorway without her bonnet. She was wonderfully beautiful, but her colour was too deep and her lovely eyes were too bright. She came straight up to me and took my arm.\n' +
        '\n' +
        '"Luncheon is ready. Was I cross, Alec? I thought I had a headache, but I haven\'t. Come here, Boris;" and she slipped her other arm through his. "Alec knows that after you there is no one in the world whom I like as well as I like him, so if he sometimes feels snubbed it won\'t hurt him."\n' +
        '\n' +
        '" la bonheur!" I cried, "who says there are no thunderstorms in April?"\n' +
        '\n' +
        '"Are you ready?" chanted Boris. "Aye ready;" and arm-in-arm we raced into the dining-room, scandalizing the servants. After all we were not so much to blame; Genevive was eighteen, Boris was twenty-three, and I not quite twenty-one.\n' +
        'II\n' +
        '\n' +
        'Some work that I was doing about this time on the decorations for Genevive\'s boudoir kept me constantly at the quaint little hotel in the Rue Sainte-Ccile. Boris and I in those days laboured hard but as we pleased, which was fitfully, and we all three, with Jack Scott, idled a great deal together.\n' +
        '\n' +
        'One quiet afternoon I had been wandering alone over the house examining curios, prying into odd corners, bringing out sweetmeats and cigars from strange hiding-places, and at last I stopped in the bathing-room. Boris, all over clay, stood there washing his hands.\n' +
        '\n' +
        'The room was built of rose-coloured marble excepting the floor, which was tessellated in rose and grey. In the centre was a square pool sunken below the surface of the floor; steps led down into it, sculptured pillars supported a frescoed ceiling. A delicious marble Cupid appeared to have just alighted on his pedestal at the upper end of the room. The whole interior was Boris\' work and mine. Boris, in his working-clothes of white canvas, scraped the traces of clay and red modelling wax from his handsome hands, and coquetted over his shoulder with the Cupid.\n' +
        '\n' +
        '"I see you," he insisted, "don\'t try to look the other way and pretend not to see me. You know who made you, little humbug!"\n' +
        '\n' +
        'It was always my rle to interpret Cupid\'s sentiments in these conversations, and when my turn came I responded in such a manner, that Boris seized my arm and dragged me toward the pool, declaring he would duck me. Next instant he dropped my arm and turned pale. "Good God!" he said, "I forgot the pool is full of the solution!"\n' +
        '\n' +
        'I shivered a little, and dryly advised him to remember better where he had stored the precious liquid.\n' +
        '\n' +
        '"In Heaven\'s name, why do you keep a small lake of that gruesome stuff here of all places?" I asked.\n' +
        '\n' +
        '"I want to experiment on something large," he replied.\n' +
        '\n' +
        '"On me, for instance?"\n' +
        '\n' +
        '"Ah! that came too close for jesting; but I do want to watch the action of that solution on a more highly organized living body; there is that big white rabbit," he said, following me into the studio.\n' +
        '\n' +
        'Jack Scott, wearing a paint-stained jacket, came wandering in, appropriated all the Oriental sweetmeats he could lay his hands on, looted the cigarette case, and finally he and Boris disappeared together to visit the Luxembourg Gallery, where a new silver bronze by Rodin and a landscape of Monet\'s were claiming the exclusive attention of artistic France. I went back to the studio, and resumed my work. It was a Renaissance screen, which Boris wanted me to paint for Genevive\'s boudoir. But the small boy who was unwillingly dawdling through a series of poses for it, to-day refused all bribes to be good. He never rested an instant in the same position, and inside of five minutes I had as many different outlines of the little beggar.\n' +
        '\n' +
        '"Are you posing, or are you executing a song and dance, my friend?" I inquired.\n' +
        '\n' +
        '"Whichever monsieur pleases," he replied, with an angelic smile.\n' +
        '\n' +
        'Of course I dismissed him for the day, and of course I paid him for the full time, that being the way we spoil our models.\n' +
        '\n' +
        'After the young imp had gone, I made a few perfunctory daubs at my work, but was so thoroughly out of humour, that it took me the rest of the afternoon to undo the damage I had done, so at last I scraped my palette, stuck my brushes in a bowl of black soap, and strolled into the smoking-room. I really believe that, excepting Genevive\'s apartments, no room in the house was so free from the perfume of tobacco as this one. It was a queer chaos of odds and ends, hung with threadbare tapestry. A sweet-toned old spinet in good repair stood by the window. There were stands of weapons, some old and dull, others bright and modern, festoons of Indian and Turkish armour over the mantel, two or three good pictures, and a pipe-rack. It was here that we used to come for new sensations in smoking. I doubt if any type of pipe ever existed which was not represented in that rack. When we had selected one, we immediately carried it somewhere else and smoked it; for the place was, on the whole, more gloomy and less inviting than any in the house. But this afternoon, the twilight was very soothing, the rugs and skins on the floor looked brown and soft and drowsy; the big couch was piled with cushionsI found my pipe and curled up there for an unaccustomed smoke in the smoking-room. I had chosen one with a long flexible stem, and lighting it fell to dreaming. After a while it went out, but I did not stir. I dreamed on and presently fell asleep.\n' +
        '\n' +
        'I awoke to the saddest music I had ever heard. The room was quite dark, I had no idea what time it was. A ray of moonlight silvered one edge of the old spinet, and the polished wood seemed to exhale the sounds as perfume floats above a box of sandalwood. Some one rose in the darkness, and came away weeping quietly, and I was fool enough to cry out "Genevive!"\n' +
        '\n' +
        'She dropped at my voice, and, I had time to curse myself while I made a light and tried to raise her from the floor. She shrank away with a murmur of pain. She was very quiet, and asked for Boris. I carried her to the divan, and went to look for him, but he was not in the house, and the servants were gone to bed. Perplexed and anxious, I hurried back to Genevive. She lay where I had left her, looking very white.\n' +
        '\n' +
        '"I can\'t find Boris nor any of the servants," I said.\n' +
        '\n' +
        '"I know," she answered faintly, "Boris has gone to Ept with Mr. Scott. I did not remember when I sent you for him just now."\n' +
        '\n' +
        '"But he can\'t get back in that case before to-morrow afternoon, andare you hurt? Did I frighten you into falling? What an awful fool I am, but I was only half awake."\n' +
        '\n' +
        '"Boris thought you had gone home before dinner. Do please excuse us for letting you stay here all this time."\n' +
        '\n' +
        '"I have had a long nap," I laughed, "so sound that I did not know whether I was still asleep or not when I found myself staring at a figure that was moving toward me, and called out your name. Have you been trying the old spinet? You must have played very softly."\n' +
        '\n' +
        'I would tell a thousand more lies worse than that one to see the look of relief that came into her face. She smiled adorably, and said in her natural voice: "Alec, I tripped on that wolf\'s head, and I think my ankle is sprained. Please call Marie, and then go home."\n' +
        '\n' +
        'I did as she bade me, and left her there when the maid came in.\n' +
        'III\n' +
        '\n' +
        'At noon next day when I called, I found Boris walking restlessly about his studio.\n' +
        '\n' +
        '"Genevive is asleep just now," he told me, "the sprain is nothing, but why should she have such a high fever? The doctor can\'t account for it; or else he will not," he muttered.\n' +
        '\n' +
        '"Genevive has a fever?" I asked.\n' +
        '\n' +
        '"I should say so, and has actually been a little light-headed at intervals all night. The idea!gay little Genevive, without a care in the world,and she keeps saying her heart\'s broken, and she wants to die!"\n' +
        '\n' +
        'My own heart stood still.\n' +
        '\n' +
        'Boris leaned against the door of his studio, looking down, his hands in his pockets, his kind, keen eyes clouded, a new line of trouble drawn "over the mouth\'s good mark, that made the smile." The maid had orders to summon him the instant Genevive opened her eyes. We waited and waited, and Boris, growing restless, wandered about, fussing with modelling wax and red clay. Suddenly he started for the next room. "Come and see my rose-coloured bath full of death!" he cried.\n' +
        '\n' +
        '"Is it death?" I asked, to humour his mood.\n' +
        '\n' +
        '"You are not prepared to call it life, I suppose," he answered. As he spoke he plucked a solitary gold-fish squirming and twisting out of its globe. "We\'ll send this one after the otherwherever that is," he said. There was feverish excitement in his voice. A dull weight of fever lay on my limbs and on my brain as I followed him to the fair crystal pool with its pink-tinted sides; and he dropped the creature in. Falling, its scales flashed with a hot orange gleam in its angry twistings and contortions; the moment it struck the liquid it became rigid and sank heavily to the bottom. Then came the milky foam, the splendid hues radiating on the surface and then the shaft of pure serene light broke through from seemingly infinite depths. Boris plunged in his hand and drew out an exquisite marble thing, blue-veined, rose-tinted, and glistening with opalescent drops.\n' +
        '\n' +
        '"Child\'s play," he muttered, and looked wearily, longingly at me,as if I could answer such questions! But Jack Scott came in and entered into the "game," as he called it, with ardour. Nothing would do but to try the experiment on the white rabbit then and there. I was willing that Boris should find distraction from his cares, but I hated to see the life go out of a warm, living creature and I declined to be present. Picking up a book at random, I sat down in the studio to read. Alas! I had found The King in Yellow. After a few moments, which seemed ages, I was putting it away with a nervous shudder, when Boris and Jack came in bringing their marble rabbit. At the same time the bell rang above, and a cry came from the sick-room. Boris was gone like a flash, and the next moment he called, "Jack, run for the doctor; bring him back with you. Alec, come here."\n' +
        '\n' +
        'I went and stood at her door. A frightened maid came out in haste and ran away to fetch some remedy. Genevive, sitting bolt upright, with crimson cheeks and glittering eyes, babbled incessantly and resisted Boris\' gentle restraint. He called me to help. At my first touch she sighed and sank back, closing her eyes, and thenthenas we still bent above her, she opened them again, looked straight into Boris\' facepoor fever-crazed girl!and told her secret. At the same instant our three lives turned into new channels; the bond that held us so long together snapped for ever and a new bond was forged in its place, for she had spoken my name, and as the fever tortured her, her heart poured out its load of hidden sorrow. Amazed and dumb I bowed my head, while my face burned like a live coal, and the blood surged in my ears, stupefying me with its clamour. Incapable of movement, incapable of speech, I listened to her feverish words in an agony of shame and sorrow. I could not silence her, I could not look at Boris. Then I felt an arm upon my shoulder, and Boris turned a bloodless face to mine.\n' +
        '\n' +
        '"It is not your fault, Alec; don\'t grieve so if she loves you" but he could not finish; and as the doctor stepped swiftly into the room, saying"Ah, the fever!" I seized Jack Scott and hurried him to the street, saying, "Boris would rather be alone." We crossed the street to our own apartments, and that night, seeing I was going to be ill too, he went for the doctor again. The last thing I recollect with any distinctness was hearing Jack say, "For Heaven\'s sake, doctor, what ails him, to wear a face like that?" and I thought of The King in Yellow and the Pallid Mask.\n' +
        '\n' +
        'I was very ill, for the strain of two years which I had endured since that fatal May morning when Genevive murmured, "I love you, but I think I love Boris best," told on me at last. I had never imagined that it could become more than I could endure. Outwardly tranquil, I had deceived myself. Although the inward battle raged night after night, and I, lying alone in my room, cursed myself for rebellious thoughts unloyal to Boris and unworthy of Genevive, the morning always brought relief, and I returned to Genevive and to my dear Boris with a heart washed clean by the tempests of the night.\n' +
        '\n' +
        'Never in word or deed or thought while with them had I betrayed my sorrow even to myself.\n' +
        '\n' +
        'The mask of self-deception was no longer a mask for me, it was a part of me. Night lifted it, laying bare the stifled truth below; but there was no one to see except myself, and when the day broke the mask fell back again of its own accord. These thoughts passed through my troubled mind as I lay sick, but they were hopelessly entangled with visions of white creatures, heavy as stone, crawling about in Boris\' basin,of the wolf\'s head on the rug, foaming and snapping at Genevive, who lay smiling beside it. I thought, too, of the King in Yellow wrapped in the fantastic colours of his tattered mantle, and that bitter cry of Cassilda, "Not upon us, oh King, not upon us!" Feverishly I struggled to put it from me, but I saw the lake of Hali, thin and blank, without a ripple or wind to stir it, and I saw the towers of Carcosa behind the moon. Aldebaran, the Hyades, Alar, Hastur, glided through the cloud-rifts which fluttered and flapped as they passed like the scolloped tatters of the King in Yellow. Among all these, one sane thought persisted. It never wavered, no matter what else was going on in my disordered mind, that my chief reason for existing was to meet some requirement of Boris and Genevive. What this obligation was, its nature, was never clear; sometimes it seemed to be protection, sometimes support, through a great crisis. Whatever it seemed to be for the time, its weight rested only on me, and I was never so ill or so weak that I did not respond with my whole soul. There were always crowds of faces about me, mostly strange, but a few I recognized, Boris among them. Afterward they told me that this could not have been, but I know that once at least he bent over me. It was only a touch, a faint echo of his voice, then the clouds settled back on my senses, and I lost him, but he did stand there and bend over me once at least.\n' +
        '\n' +
        'At last, one morning I awoke to find the sunlight falling across my bed, and Jack Scott reading beside me. I had not strength enough to speak aloud, neither could I think, much less remember, but I could smile feebly, as Jack\'s eye met mine, and when he jumped up and asked eagerly if I wanted anything, I could whisper, "YesBoris." Jack moved to the head of my bed, and leaned down to arrange my pillow: I did not see his face, but he answered heartily, "You must wait, Alec; you are too weak to see even Boris."\n' +
        '\n' +
        'I waited and I grew strong; in a few days I was able to see whom I would, but meanwhile I had thought and remembered. From the moment when all the past grew clear again in my mind, I never doubted what I should do when the time came, and I felt sure that Boris would have resolved upon the same course so far as he was concerned; as for what pertained to me alone, I knew he would see that also as I did. I no longer asked for any one. I never inquired why no message came from them; why during the week I lay there, waiting and growing stronger, I never heard their name spoken. Preoccupied with my own searchings for the right way, and with my feeble but determined fight against despair, I simply acquiesced in Jack\'s reticence, taking for granted that he was afraid to speak of them, lest I should turn unruly and insist on seeing them. Meanwhile I said over and over to myself, how would it be when life began again for us all? We would take up our relations exactly as they were before Genevive fell ill. Boris and I would look into each other\'s eyes, and there would be neither rancour nor cowardice nor mistrust in that glance. I would be with them again for a little while in the dear intimacy of their home, and then, without pretext or explanation, I would disappear from their lives for ever. Boris would know; Genevivethe only comfort was that she would never know. It seemed, as I thought it over, that I had found the meaning of that sense of obligation which had persisted all through my delirium, and the only possible answer to it. So, when I was quite ready, I beckoned Jack to me one day, and said\n' +
        '\n' +
        '"Jack, I want Boris at once; and take my dearest greeting to Genevive...."'
